.subTotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
}
.subTotal__gift{
    display: flex;
    align-items: center;
}
.subTotal__gift > input{
    margin-right: 5px;
}

.subTotal > button {
    background: #f0c14b;
    border-radius: 3px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top:10px;
    border-color: #a88734 #9c7e31 #8462a9;
    color: #111;
}

@media screen and (max-width: 600px) {
    .subTotal{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 150px;
        height: 100px;
        padding: 10px;
        background-color: #f3f3f3;
        border: 1px solid #dddddd;
        border-radius: 3px;
    }
    
  }