.checkout{
    display: flex;
    padding: 20px;
    background-color: white;
    height: max-content;
}
.checkout__ad{
    width: 100%;
    margin-bottom: 10px;
}
.checkout__title{
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}
.checkout__left{
    display: flex;
    flex-direction: column;
    flex: 0.7;

}
.checkout__right{
    display: flex;
    flex: 0.3;
   

}
.checkoutproducts{
    
    box-shadow: black;
}

@media screen and (max-width: 600px) {
    .checkout{
        display: flex;
        padding: 10px;
        background-color: white;
        height: max-content;
        width:580px;
        
    }
    .checkout__right{
        width: 50%;
    }

  }